.single {
  display: flex;
  width: 100%;
  .singleContainer {
    flex: 6;

    .top {
      padding: 20px;
      display: flex;
      gap: 20px;

      .left {
        flex: 1;
        -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
        box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
        padding: 20px;
        position: relative;

        .editButton {
          position: absolute;
          top: 0;
          right: 0;
          padding: 5px;
          font-size: 12px;
          color: #7451f8;
          background-color: #7551f818;
          cursor: pointer;
          border-radius: 0px 0px 0px 5px;
        }
        

        .item {
          display: flex;
          gap: 20px;

        }
          

          .details {
            .itemTitle {
              margin-bottom: 10px;
              color: #555;
            }

            .detailItem {
              margin-bottom: 10px;
              font-size: 16px;

              .itemKey {
                font-weight: bold;
                color: gray;
                margin-right: 5px;
              }

              .itemValue {
                font-weight: 300;
              }
            }
          }
        }
      }

      .right {
        flex: 2;
        display: flex;
      }
    }

    .bottom {
      -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
      box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
      padding: 20px;
      margin: 10px 20px;
    
      .item-photos {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;

        .arrow {
          margin: 5px;
          font-size: 30px;
          color: lightgray;
          cursor: pointer;
        }

        .slider-wrapper {
          display: flex;
          align-items: center;

          .item-images {
            width: auto;
            height: auto;
            object-fit: cover;
          }
        }
      }
    }
   .title {
    font-size: 16px;
    color: lightgray;
    margin-bottom: 20px; 
   }
  }


