.table {
  .table-rating {
    display: flex;
    justify-content: space-evenly;
    line-height: 18px;
  .adc357e4f1 {
    fill: #febb02;
    height: 14px;
  }
  .b6dc9a9e69 svg {
    display: inline-block;
    height: 100%;
    vertical-align: top;
}}
  .cellWrapper {
    display: flex;
    align-items: center;

    .image {
      width: 130px;
      height: 130px;
      border-radius: 2px;
      margin-right: 10px;
      object-fit: cover;
    }
  }

  .status {
    padding: 5px;
    border-radius: 5px;

    &.Approved {
      color: green;
      background-color: rgba(0, 128, 0, 0.151);
    }
    &.Pending {
      color: goldenrod;
      background-color: rgba(189, 189, 3, 0.103);
    }
  }
}
